import React, {useEffect} from "react";
import { useFetch } from '../hooks/useFetch';
import { useTodoStore } from "../stores/todoStore";

function setLazyLoadEndTime() {
  const { setLazyLoadTime } = useTodoStore.getState();
  setLazyLoadTime('lazyLoadEndTime', performance.now());
}

const GrandchildComponent: React.FC = () => {
  const { data, loading, error } = useFetch('https://microsoftedge.github.io/Demos/json-dummy-data/5MB.json');

  useEffect(()=> {
    if (data) setLazyLoadEndTime();
  }, [data]);

  if (loading) return <div>Loading Grandchild component</div>
  if (error) return <div>{`Error Grandchild component ${error}`}</div>
  if (data) console.log('Grandchild data: ', data);

  return(
    <div>Grandchild component</div> 
  );
};

export default GrandchildComponent;
